import React from "react";
import Navbar from "./components/Navbar";
import "./css/style.css";
import "./css/animate.css";
import "./css/flaticon.css";
import "./css/icomoon.css";
import "./css/ionicons.min.css";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Home from "./pages/Home";
import About from "./pages/About";
import Contact from "./pages/Contact";
import SinglePost from "./pages/SinglePost";
import AddPost from "./pages/AddPost";
import {
  Stitch,
  AnonymousCredential,
  RemoteMongoClient,
} from "mongodb-stitch-browser-sdk";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      comments: [],
      posts: [],
    };

    this.displayComments = this.displayComments.bind(this);
    this.displayPosts = this.displayPosts.bind(this);
  }

  componentDidMount() {
    this.client = Stitch.initializeDefaultAppClient("blog-jcusl");
    // Get a MongoDB Service Client
    // This is used for logging in and communicating with Stitch
    const mongodb = this.client.getServiceClient(
      RemoteMongoClient.factory,
      "mongodb-atlas"
    );
    // Get a reference to the todo database
    this.db = mongodb.db("Blog");
  }

  // getting all blog posts and displaying snippets
  displayPosts() {
    // query the remote DB and update the component state
    this.db
      .collection("posts")
      .find({}, { limit: 1000 })
      .asArray()
      .then((posts) => {
        console.log(posts);
        this.setState({ posts });
      });
  }

  displayPostsonLoad() {
    // Anonymously log in and display comments on load
    this.client.auth
      .loginWithCredential(new AnonymousCredential())
      .then(this.displayPosts)
      .catch(console.error);
  }

  // displaying comments for a particular blog post
  displayComments() {
    // query the remote DB and update the component state
    this.db
      .collection("comments")
      .find({}, { limit: 1000 })
      .asArray()
      .then((comments) => {
        console.log(comments);
        this.setState({ comments });
      });
  }

  displayCommentsonLoad() {
    // Anonymously log in and display comments on load
    this.client.auth
      .loginWithCredential(new AnonymousCredential())
      .then(this.displayComments)
      .catch(console.error);
  }

  render() {
    return (
      <BrowserRouter>
        <Navbar />
        <Switch>
          <Route path="/personal-blog/about">
            <About />
          </Route>
          <Route path="/personal-blog/contact">
            <Contact />
          </Route>
          <Route exact path="/personal-blog">
            <Home posts={this.state.posts} />
          </Route>
          <Route
            path="/personal-blog/singlepost/:id"
            component={SinglePost}
            displayCommentsonLoad={this.displayCommentsonLoad}
          ></Route>
          <Route path="/personal-blog/add-post">
            <AddPost />
          </Route>
        </Switch>
      </BrowserRouter>
    );
  }
}

export default App;
